<template>
    <section>
        <div id="addGroupRecord">
            <Form ref="AddGroup">
                <div class="row">
                    <div class="col-lg-12">
                        <dynamic-form lang="en" ref="AddGroup1" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></dynamic-form>
                    </div>
                    <div class="col-lg-12 mb-3" id="divtickettupelist">
                        <div class="listing">
                            <div class="table-responsive table-fix-header" id="divUserList"
                                style="max-height: 220px;">
                                <table id="example" class="table table-bordered dt-responsive nowrap mt-0 w-100">
                                    <thead class="thead-bg">
                                        <tr>
                                            <th scope="col">{{ $t('CAusername') }}</th>
                                            <th scope="col">{{ $t('Department') }}</th>
                                            <th scope="col" class="text-center">{{ $t('Action') }}</th>
                                        </tr>
                                    </thead>
                                    <tbody id="UserListDiv">
                                        <tr v-for="itemData in UserList" :key="itemData">
                                            <td>{{ itemData.userName }}</td>
                                            <td>{{ itemData.departmentName }}</td>
                                            <td class='text-center single-action'><a
                                                    class='ancAddUsers round-icon-small btn-success-light d-block float-none m-auto'
                                                    href='javascript:;'
                                                    @click="AssignedUser(itemData.userId, itemData.userName, itemData.departmentName)"
                                                    :data-id="itemData.userId" :data-name="itemData.UserName"
                                                    :data-department="itemData.departmentName"><i class='fa fa-plus'
                                                        alt='Click to Add' title='Add'></i></a></td>
                                        </tr>
                                        <tr class='norecords' id='trNoRecordFond' v-if="UserList.length == 0">
                                            <td colspan='5' class='text-danger text-center'>{{ $t('NoRecordfound')
                                                }}
                                            </td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                        </div>
                        <span id="error_User" v-if="UserList.length == 0" class="text-danger">{{
                            $t('CheckAtleastOneUser')
                            }}</span>
                    </div>
                    <div class="col-lg-12">
                        <div class="listing">
                            <div class="table-responsive table-fix-header">
                                <table class="table table-bordered" id="DefaultUserTable">
                                    <thead class="thead-bg">
                                        <tr>
                                            <th scope="col">{{ $t('CAusername') }}</th>
                                            <th scope="col">{{ $t('Department') }}</th>
                                            <th scope="col" width="200">{{ $t('MaxTicketAssign') }}</th>
                                            <th scope="col" class="text-center">{{ $t('MarkAsDefault') }}</th>
                                            <th scope="col" class="text-center">{{ $t('Action') }}</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr v-for="(item, i) in AssignUserList" :key="item">
                                            <td>{{ item.name }}</td>
                                            <td>{{ item.departmentName }}</td>
                                            <td class="text-center">
                                                <input type="text"
                                                    :value="item.UserId == item.DefaultUser ? MaxTicketAssign : ''"
                                                    :name="'maxticketassign-' + item.Id"
                                                    :disabled="item.Id != defaultUser" :class="{
                                                        'form-control': true,
                                                        'is-invalid': !isMaxTicketValid && (!item.Id || (item.Id != defaultUser && !MaxTicketAssign.trim())) && item.disabled
                                                    }" maxlength="4" tabindex="6"
                                                    oninput="this.value = this.value.replace(/[^0-9]/g, '').slice(0, 4);">
                                            </td>
                                            <td class="text-center">
                                                <div class="d-inline-block"><input type="radio"
                                                        v-model="defaultUser" @click="SetValue(item.Id)"
                                                        :value="item.Id" :id="item.Id"
                                                        :checked="item.UserId != item.DefaultUser" name="default"
                                                        tabindex="7"></div>
                                            </td>
                                            <td class="text-center">
                                                <a class="aRemoveUser round-icon-small btn-danger-light d-block float-none m-auto"
                                                    @click="DeleteUser(item.Id)" href="javascript:;"><i
                                                        class=" fa fa-trash" title='Add'></i></a>
                                            </td>
                                        </tr>
                                        <tr class='norecords' v-if="AssignUserList.length == 0">
                                            <td colspan='5' class='text-danger text-center'>{{ $t('NoRecordfound')
                                                }}
                                            </td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                        </div>
                        <span id="error_DefaultUser"
                            v-if="(CheckAssignedUser && AssignUserList.length == 0) || checkOneUserSelected"
                            class="text-danger">Check Atleast One Defualt User</span>
                    </div>
                    <div class="col-lg-12 mt-3">
                        <Field name="Description" v-slot="{ errors }" :rules="{ 'required': true }">
                            <label>Description :<span class="text-danger">*</span></label>
                            <div class="form-group">
                                <textarea v-model="Description" tabindex="8" maxlength="500"
                                    :class="{ 'form-control': true, 'is-invalid': errors[0] }"></textarea>
                                <span v-if="errors[0]" name="Description" class="invalid-feedback d-block">{{
                                    errors[0]
                                }}</span>
                            </div>
                        </Field>
                    </div>
                    <div class="col-12">
                    <div class="row flex-row-reverse mt-3">
                            <div class="col-lg-12 text-right">
                                <button title="Previous" @click="PreviousButton()" class="btn btn-warning float-left">Previous</button>
                                <button title="Skip" @click="Skip()" class="popupgreenbtn btn skip-btn-clr formbtn widthhalf px-4">Skip</button>
                                <button @click="SubmitData(values)" type="button" style="margin-left:5px" class="popupgreenbtn btn btn-success formbtn widthhalf pr-2"
                                    id="aGroupTicketSave" title="" data-toggle-tooltip="tooltip"
                                    data-original-title="Save"><i class="fa fa-save pr-2"></i>  {{$t('SaveAndNext')}}</button>
                            </div>
                    </div>
                </div>
                </div>
            </Form>
        </div>

    </section>
</template>
<script>
import setupModuleService from "@/services/AdvanceSetupModuleService";
export default {
    props: {
        tabCount: 0,
        currentTabIndex: 0
    },
    created: async function () {
        var vm = this;
        await vm.GetPrivilegeData();
        vm.GetDepartment();
        vm.GetResolveTicketUserList();
        vm.GetGroupTicketData(0);
    },
    data() {
        return {
            UserList: [],
            AssignUserList: [],
            Description: '',
            defaultUser: '',
            GroupType: '',
            MaxTicketAssign: '',
            isMaxTicketValid: true,
            CheckAssignedUser: false,
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: 'Ticket Group Name',
                            name: "TicketGroupName",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true, max: 250 },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Skill'),
                            name: "SkillId",
                            value: "",
                            placeholder: "",
                            visibility: true,
                            config: {
                                options: [],
                                showAddIcon: false,
                                onChange: this.GetResolveTicketUserList
                            },
                            validationRules: "",
                        },
                    ]
                },
                {
                    layoutType: "double",
                    Data: [

                        {
                            astype: "SelectField",
                            label: this.$t('Department'),
                            name: "DepartmentId",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon: false,
                                onChange: this.GetResolveTicketUserListIs
                            },
                            validationRules: "",
                        },
                        {
                            astype: "RadioField",
                            label: this.$t('GroupType'),
                            name: "GroupType",
                            value: "Call",
                            placeholder: "",
                            config: {
                                options: [{
                                    name: 'Call', value: 'Call'
                                }, {
                                    name: 'Routing', value: 'Escalate'
                                }],
                                showAddIcon: false,
                                onChange: this.onChangeRadio
                            },
                            validationRules: { "required": false },
                        },
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-warning",
                    text: "<i class='fa fa-save pr-2'></i>Save Of Draft",
                    isVisible: false
                }
            ],
        }
    },
    methods: {
        Skip: function () {
            var vm = this;
            vm.$emit("nextTab")
        },
        PreviousButton: function () {
            var vm = this;
            vm.$emit("previousTab");
        },
        GetPrivilegeData: async function () {
            var vm = this;
            var param = 'controller=Review&action=Index';
            await setupModuleService.CheckPrivilege(param).then(function (response) {
                if (response.data) {
                    vm.FormSchema[0].Data[1].visibility = true;
                    vm.GetSkill();
                }
                else { vm.FormSchema[0].Data[1].visibility = false; }
            })
        },
        GetSkill: function () {
            var vm = this;
            setupModuleService.GetSkill().then(response => {
                response.data.forEach((item, index) => {
                    this.FormSchema[0].Data[1].config.options.push({
                        value: item.skill_id,
                        name: item.title
                    })
                })
                vm.$refs.AddGroup1.UpdateKeyValue();
            })
        },
        GetDepartment: function () {
            setupModuleService.GetDepartment().then(response => {
                response.data.forEach((item, index) => {
                    this.FormSchema[1].Data[0].config.options.push({
                        value: item.department_id,
                        name: item.department_name
                    })
                })
                this.$refs.AddGroup1.UpdateKeyValue();
            })
        },
        GetResolveTicketUserList: function (e, field) {
            var vm = this;
            if (field != undefined) {
                var params = `departmentId=&skillId=${field.value}`
            }
            else {
                var params = `departmentId=&skillId=`
            }

            setupModuleService.GetResolveTicketUserListbydept(params).then(response => {
                vm.UserList = response.data;
            })
        },
        GetResolveTicketUserListIs: function (e, field) {
            var vm = this;
            var params = `departmentId=${field.value}&skillId=`
            setupModuleService.GetResolveTicketUserListbydept(params).then(response => {
                vm.UserList = response.data;
            })
        },
        AssignedUser: function (id, name, departmentname) {
            var vm = this;
            if (vm.AssignUserList.some(item => item.Id === id)) {
                vm.ShowAlert(name + ' ' + vm.$t('AlreadyExist'), "warning");
            }
            else {
                vm.AssignUserList.push({
                    Id: id,
                    name: name,
                    departmentName: departmentname,
                    UserId: id
                })
            }
        },
        DeleteUser: function (id) {
            var vm = this;
            vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Group")), function () {
                vm.AssignUserList = jQuery.grep(vm.AssignUserList, function (value) {
                    return value.Id != id;
                });
            });
        },
        GetGroupTicketData: function (id) {
            var vm = this;
            var params = `ticketGroupId=${id}`
            setupModuleService.GetGroupTicketData(params).then(response => {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        if (response.data.Data.length > 0) {
                            vm.FormSchema[0].Data[0].value = response.data.Data[0].ticket_group_name;
                            vm.Description = response.data.Data[0].description;
                            vm.FormSchema[1].Data[0].value = response.data.Data[0].department_id;
                            vm.FormSchema[1].Data[1].value = response.data.Data[0].Group_type;
                            vm.MaxTicketAssign = response.data.Data[0].max_ticket_assign;
                            vm.defaultUser = response.data.Data[0].default_user_id;
                            response.data.Data.forEach((item, index) => {
                                vm.AssignUserList.push({
                                    Id: item.user_id,
                                    name: item.username,
                                    departmentName: item.department_name,
                                    UserId: item.user_id,
                                    DefaultUser: item.default_user_id
                                })
                            })

                        }
                    }
                }
            })
        },

        SubmitData: function () {
            var vm = this;
            var userIds = ''
            vm.AssignUserList.forEach((item, index) => {
                if (userIds == '') {
                    userIds += item.Id
                }
                else {
                    userIds += ',' + item.Id
                }

            })

            vm.AssignUserList.forEach((item) => {
                const inputElement = document.querySelector(`input[name="maxticketassign-${item.Id}"]`);

                if (inputElement && !inputElement.disabled) {
                    const maxTicketAssignValue = inputElement.value.trim();

                    if (!maxTicketAssignValue) {
                        vm.isMaxTicketValid = false;
                        vm.MaxTicketAssign = '';
                        inputElement.classList.add('is-invalid');
                        return false;
                    } else {
                        vm.isMaxTicketValid = true;
                        vm.MaxTicketAssign = maxTicketAssignValue;
                        inputElement.classList.remove('is-invalid');
                    }
                }
            });

            vm.$refs.AddGroup.validate().then(result => {
                if ((vm.AssignUserList.length == 0 || vm.isBlank(vm.defaultUser))) {
                    vm.CheckAssignedUser = true;
                    vm.ShowAlert(vm.$t('CorrectHighlightedErrors'), "warning");
                    vm.checkOneUserSelected = true;
                    return false;
                }

                if (result) {
                    var params = `groupTicketId=0&groupName=${vm.FormSchema[0].Data[0].value}&description=${vm.Description}&departmentId=${vm.FormSchema[1].Data[0].value == '' ? 0 : vm.FormSchema[1].Data[0].value}&groupType=${vm.FormSchema[1].Data[1].value}&maxTiacketAssign=${vm.MaxTicketAssign}&userIds=${userIds}&defaultUser=${vm.defaultUser}`
                    setupModuleService.AddTicketGroup(params).then(response => {
                        if (response.data == true) {
                            vm.ShowAlert(vm.$t('GroupHasAdded'), "success");
                            if (vm.tabCount > 1) {
                                vm.$emit("nextTab")
                                vm.$emit("updateProgress", currentTabIndex, true);
                            }
                        }
                        else {
                            vm.ShowAlert(vm.$t('AddedError'), "failure");
                        }
                    })
                }
            })
        },
        isNumber: function (evt) {
            evt = (evt) ? evt : window.event;
            var charCode = (evt.which) ? evt.which : evt.keyCode;
            if ((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46) {
                evt.preventDefault();;
            } else {
                return true;
            }
        },
        SetValue: function (id, e) {
            var vm = this;
            vm.MaxTicketAssign = '';
            vm.defaultUser = id;
        },
        onChangeRadio: function (e, field) {
            var vm = this;
            vm.FormSchema[1].Data[1].value = e.target.dataset.value;
        }
    }
}

</script>
<style scoped>
/* CSS for validation error */
.is-invalid {
    border-color: red;
}
</style>